* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@import url("https://fonts.googleapis.com/css?family=Raleway:400,400i,700");


.app {
  text-align: center;
  background-color: rgb(25, 40, 53);
  min-height: 100vh;
  width: 100vw;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

h2 {
  font-family: Raleway, sans-serif;
  color: rgba(74, 74, 74, 0.5);
  margin-top: 3vh;
  margin-bottom: -1vh;
  font-size: calc(10px + 4vmin);
}

#wheel{
  height: 7vmin;
  width: 7vmin;
  opacity: 0.3;
  margin: 0;
  padding: 0;
}

.turning {
  height: 7vmin;
  width: 7vmin;
  animation: turn 10s linear infinite;
  animation-play-state: running;
  margin: 0vmin;
}

.turning.active {
  animation-play-state: paused;
}

@keyframes turn {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(1turn);
  }
} 


.wrapper {
  background-color: #051a21;
  border-radius: 20px;
  height: 90vh;
  display: flex;
  flex-direction: column;
  display: flex;
  align-items: center;
  box-shadow: 0 0 12px 6px hsl(200, 100%, 50%);
  margin: 0;
}

.session {
  width: 40vmin;
  color: slategray;
  font-family: Raleway, sans-serif;
  font-size: calc(5px + 1vmin);
  margin-top: 2vmin;
}

.break {
  width: 40vmin;
  color: slategray;
  font-family: Raleway, sans-serif;
  font-size: calc(5px + 1vmin);
}

.session-modified{
  margin-top: -2vh;
  font-size: calc(0.8rem + 1vmin);
  color: rgb(182, 23, 23);
  font-weight: bolder;
}

.break-modified {
  color: rgb(182, 23, 23);
  font-size: calc(0.8rem + 1vmin);
  font-weight: bolder;
}

.changeMinutes {
  border: none;
  width: calc(10px + 6vmin);
  height: calc(10px + 6vmin);
  border-radius: 50%;
  /* background: radial-gradient(ellipse at center, #29b8ff, #0078a4); */
  background: radial-gradient(ellipse at center, #00AAFF, #032936);
  box-shadow: 0 0 8px 2px hsl(200, 100%, 50%);
  color:khaki;
  font-size: calc(10px + 5vmin);
  transition: color 0.3s ease;
}

.changeMinutes:hover {
  color: darkred;
}

.time-inactive {
  color: slategray;
  font-size: calc(20px + 7vmin);
  display: flex;
  margin: 5vmin;
  justify-content: center;
}

.time-active {
  color: darkorange;
  font-size: calc(20px + 7vmin);
  display: flex;
  margin: 5vmin;
  justify-content: center;
}

.time-active-breakon {
  color: rgb(60, 202, 60);
  font-size: calc(20px + 7vmin);
  display: flex;
  justify-content: center;
  margin: 5vmin;
}

.mins {
 width: calc(50px + 10vmin);
 margin-right: 2vmin;
 min-width: 80px;
 display: flex;
 justify-content: flex-end;
}

.secs {
  margin-left: 2vmin;
  width: calc(50px + 10vmin);
  min-width: 80px;
  display: flex;
}

span {
  position: absolute;
}


.session-sign-inactive,
.session-sign-active {
  display: inline-block;
  width: 35%;
  min-height: 7vh;
  font-family: 'Raleway', sans-serif;
  font-size: calc(0.5rem + 2vmin);
  vertical-align: super;
}

.session-sign-active {
  color: darkorange;
}


.break-sign-inactive,
.break-sign-active {
  display: inline-block;
  width: 35%;
  font-family: 'Raleway', sans-serif;
  font-size: calc(0.5rem + 2vmin);
  vertical-align: super;  
}

.break-sign-active {
  color: rgb(60, 202, 60);
}


.button,
.button-reset {
  width: 15vmin;
  height: 15vmin;
  border-radius: 50%;
  margin: 2rem 1rem;
  font-family: 'Raleway', sans-serif;
  font-size: calc(10px + 1.5vmin);
  color: hsl(200, 100%, 16%);
  box-shadow: 0 0 10px 2px hsl(200, 100%, 50%);
  border: none;
}

.button-reset {
  background: radial-gradient(ellipse at center, #00AAFF, #032936);
  font-weight: 500;
  color: darkred;
}


.button:focus {
  outline-width: 0;
}

.button-primary-active {
  background: radial-gradient(ellipse at center, #00AAFF, #032c3b);
}

.button-primary-inactive {
  background: radial-gradient(ellipse at center, #00aaff, #032531);
}

.button-primary-inactive:hover {
  background: radial-gradient(ellipse at center, #20b5ff, #032c3b);
}

.button-primary-active:hover {
  background: radial-gradient(ellipse at center, #29b8ff, #032c3b);
}

.button-reset:hover {
  background: radial-gradient(ellipse at center, #29b8ff, #032c3b);
}

.button-reset:focus {
  outline-width: 0;
}

.row {
  margin-top: 6vh;
}
